@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  text-decoration: none;
  color: black;
}

.link {
  display: inline-block;
  width: 100px;
  border: 1px solid rgb(93, 195, 243);
  border-radius: 5px;
  color: rgb(80, 106, 255);
  text-decoration: none;
  text-align: center;
  padding: 5px;
  margin: 25px;
}

nav > .link:hover {
  box-shadow: 0px 0px 20px rgb(182, 225, 236);
}

a:hover {
  text-shadow: 0px 0px 20px rgb(121, 80, 255);
}

.header-1 {
  text-align: center;
  -webkit-text-decoration: dashed;
          text-decoration: dashed;
  color: rgb(0, 17, 255);
}
  
.img-header {
    
}

.img-home {
    width: 100%;
    height: 575px;
    text-align: center;
}

.text-header {
    text-align: center;
    -webkit-text-decoration: dashed;
            text-decoration: dashed;
    color: rgb(0, 17, 255);
}
  
.post {
    padding: 12px;
    display: flex;
    box-shadow: 0 0 7px #607d8b;
    margin: 20px 0px;
    max-width: 760px;
	width: 100%;
	padding: 10px;
	box-sizing: border-box;
	margin: 0 auto;
}

.avatar {
    flex: 0 0 auto;
}

.oficial-icon {
    width: 24px;
    fill: #4caf50;
}

.avatar img {
    height: 50px;
    width: 50px;
    border-radius: 50%;
    display: block;
    object-fit: cover;
}

.post__container {
    padding-left: 10px;
}

.post__header {
    position: relative;
    font-size: 18px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-family: "Roboto", sans-serif;
}

.post__header-wrapper {
    display: flex;
}

.post-button-icon {
    width: 20px;
    cursor: pointer;
}

.post-button-icon:hover {
    fill: #4caf50;
}

.author-name {
    color: #2196f3;
    display: flex;
    flex-wrap: wrap;
    font-weight: 700;
}

.author-nickname {
    color: #9e9e9e;
    padding: 0px 5px;
}

.author-nickname::after {
    content: "·";
    padding-left: 5px;
}
.post__date {
    color: #9e9e9e;
}

.post__text {
    margin: 10px 0px;
    font-family: "Roboto", sans-serif;
    font-size: 18px;
    font-weight: 400;
}

.post__img {
    width: 630px;
    height: 300px;
    object-fit: cover;
    border-radius: 24px;
}

.post__footer {
    display: flex;
    justify-content: space-around;
    padding: 20px 0px;
}

.icons {
    width: 26px;
}

.post__answer,
.post__retweet,
.post__like,
.post__share {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.post__answer:hover .icons {
    fill: #2196f3;
}

.post__retweet:hover .icons {
    fill: #9c27b0;
}

.post__like:hover .icons {
    fill: #f44336;
}

.post__share:hover .icons {
    fill: #2196f3;
}

@media (max-width: 460px) {
    .post__header {
        font-size: 16px;
    }
    .post__container {
        padding-left: 5px;
    }
}

.photo-box {
    display: flex;
    flex-flow: wrap;
    justify-content: center;
    align-items: center;
}

.img_Venice {
    width: 25rem;
    height: 15rem;
    margin: 15px 10px;
}

body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
    font-size: 20px;
    background-color: rgb(216, 236, 235);
  }
  
  .wrapper {
    margin: 15px auto;
    border-radius: 20px;
    background-color: rgb(164, 204, 245);
    width: 350px;
    padding: 15px 25px;
    align-items: center;
    align-content: center;
  }
  
  .icon img {
    width: 25px;
    height: 25px;
  }
  
  .phone img {
    width: 25px;
    height: 25px;  
    cursor: pointer;
  }
  
  .container {
    display: flex;
  }
  
  .contacts_wrapper {
    max-width: 300px;
    margin: auto;
  }
  
  .info {
    margin-left: 10px;
  }
  
  .main_info {
    display: flex;
  
  }
  
  .contact {
    font-family: 'Open Sans', sans-serif;
    font-weight: 900px;
    font-size: 30px;
    text-align: center;
    color: rgb(34, 26, 26);
  }
  
  input {
    margin: 25px 62px;
    border: none;
    border-radius: 20px;
    padding: 5px 5px;
    background: rgb(212, 221, 238);
    outline: none;
    height: 30px;
    color: black;
    font-size: 20px;
    font-family: Georgia, 'Times New Roman', Times, serif;
   }
  
  input[placeholder] {
    text-align: center;
  }
  
  .contacts {
    cursor: pointer;
  }
  
  .contacts:hover {
    color: rgb(166, 208, 243);
  }
